header {
    font-size: .9em;
    font-weight: 400;
    font-family: $secondary-font;
    padding-top: 1.5em;
    padding-bottom: 1em;

    .logo {
        font-weight: 400;
        text-decoration: none;
        border-bottom: none;
        font-size: 1em;
        color: $primary-color;
        border-bottom: 1px solid lighten($primary-font-color,90%);

        transition: color 200ms, border-bottom 200ms;
        -webkit-transition: color 200ms, border-bottom 200ms; }

    .logo:hover {
        border-bottom: 1px solid $primary-color;
        transition: color 200ms, border-bottom 200ms;
        -webkit-transition: color 200ms, border-bottom 200ms; }

    .line {
        color: lighten($primary-font-color,50%);
        margin: 0 .5em; }

    .txt-rotate {
        color: lighten($primary-font-color,50%);
        font-size: .8em; }

    .social {
        text-align: right;
        font-size: 1.4em;

        .separacion {
            margin-left: 1em; }

        a {
            border-bottom: none;
            color: #000; }

        a:hover {
            color: $primary-color; } } }

/* Selectors and styles affecting screens UP TO 480px (Phone) */
@media (max-width: $breakpoint-small) {
    .line, .txt-rotate, .blinking-cursor {
        display: none;
        visibility: hidden; } }
