.page-post {
    margin-top: 2em;

    .postInfo {
        text-align: center;
        .title {
            font-size: 2em;
            font-weight: 400;
            line-height: 1.4em;
            color: $primary-color; }
        .info {
            font-size: .8em;
            .icon-minus {
                margin: 0 .5em;
                color: lighten($primary-font-color,50%); } } }

    .postContent {
        h1, h2, h3, h4, h5, h6 {
            font-weight: 400;
            margin-block-start: 2em; }

        p {
            display: block;
            margin-block-start: 1em;
            margin-block-end: 2.5em;
            margin-inline-start: 0px;
            margin-inline-end: 0px; }

        img {
            max-width: 100%; }

        a {
            color: $primary-color; }

        figure {
            margin: 0;
            text-align: center;

            figcaption {
                font-size: .85em;
                line-height: 1.8em;
                padding: 0 1.5em 2em 1.5em; } }

        .key {
            font-weight:      400;
            background-color: #f7f7f7;
            border:           1px solid #ccc;
            color:            #333;
            font-size:        .8em;
            line-height:      1.4em;
            text-shadow:      0 1px 0 #fff;
            display:          inline-block;
            padding:          0.1em 0.6em;
            margin:           0 0.1em;
            white-space:      nowrap;

            -webkit-box-shadow: 0 1px 0px rgba(0,0,0,0.2),0 0 0 2px #fff inset;
            -moz-box-shadow:    0 1px 0px rgba(0,0,0,0.2),0 0 0 2px #fff inset;
            box-shadow:         0 1px 0px rgba(0,0,0,0.2),0 0 0 2px #fff inset;

            -webkit-border-radius: 3px;
            -moz-border-radius:    3px;
            border-radius:         3px; }

        .terminal {
            background: #262626;
            color: white;
            padding: 1em;
            font-family: 'Maven Pro', sans-serif; }

        .information,.warning {
            border-left: 2px solid #f9f9f9;
            background: #f9f9f9;
            padding: 1em; }

        .information {
            border-left-color: $primary-color; }

        .warning {
            border-left-color: $secondary-color; }

        .shareAndDonation {
            text-align: center;
            margin-bottom: 1em;
            padding: 0 1em; } } }
