.page-index {
    text-align: center;
    padding-top: 2em;

    p.hi {
        font-size: 1.7em;
        line-height: 2em;
        font-family: $secondary-font;
        margin-bottom: .5em;
        .name {
            color: $primary-color;
            font-weight: 400; } }

    p.aboutMeInfo {
        padding-top: 1em; }

    p.aboutMe {
        a {
            color: $primary-color; } } }

.page-blog {
    .year, .categories, .tags {
        color: $secondary-color;
        font-weight: 400;
        font-family: $secondary-font;
        font-size: .9em;
        text-transform: uppercase; }

    ul {
        list-style: none;
        padding-inline-start: 0;
        margin-top: 0; }

    .allCategories {
        li {
            a {
                cursor: pointer; } } }

    .article {
        margin: 0 0 2em 0;
        .title {
            line-height: 1.6em;
            a {
                border-bottom: none;
                &:hover {
                    .icon-link {
                        color: $primary-color; } } } }
        .info {
            font-size: .8em;
            .category {
                a {
 } }                    //border-bottom: none
            .icon-minus {
                margin: 0 .5em;
                color: lighten($primary-font-color,50%); } } }

    .hideArticle {
        display: none;
        visibility: hidden; } }
